import wPicker from "@/components/w-picker/w-picker.vue"; 
import contact from "@/components/contact/contact.vue"
import banner from "@/components/banner/banner.vue"
import counter from "@/components/counter/counter.vue"
export default {
	components:{
		contact,counter,banner,wPicker
	},
	data() {
		return {
			title: 'Hello',
			num18:1,
			price:9.99,
			province:"",
			city:"",
			area:"",
			address:'',
			phone:'',
			addressDetail:'',
			reciver:""
			
		}
	},
	onLoad() {

	},
	methods: {
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		// pay(){
			
		// },
		onConfirm(address){
			console.log(address);
			this.province = address.checkArr[0];
			this.city = address.checkArr[1];
			this.area = address.checkArr[2];
			
			this.address = address.checkArr[0]+address.checkArr[1]+address.checkArr[2]
		},
		openAddress(){
			this.$refs.picker.show()
		},
		setNum(num18){
			this.num18 = num18
		},
		pay(){
			let _this = this
			if(this.reciver == ""){
				uni.showToast({
					"title":"请添加收货人",
					"icon":"none"
				})
				return false;
			}
			if(this.phone == ""){
				uni.showToast({
					"title":"请填写手机",
					"icon":"none"
				})
				return false;
			}

			if(!(/^1[3456789]\d{9}$/.test(_this.phone))){
				uni.showToast({
					"title":"手机号码格式不正确",
					"icon":"none"
				}) 
				return false; 
			}
			if(this.num18 == 0){
				uni.showToast({
					"title":"数量不能为0，请添加数量",
					"icon":"none"
				})
				return false;
			}
			
			if(this.address == ""){
				uni.showToast({
					"title":"请输入详细地址",
					"icon":"none"
				})
				return false;
			}
			
			if(this.addressDetail == ""){
				uni.showToast({
					"title":"想输入详细地址",
					"icon":"none"
				})
				return false;
			}
			// let str = 
			uni.showModal({
				"title":(_this.price*_this.num18)+"元",
				"content":"收货人："+_this.reciver+"("+_this.phone+")\n收货地址："+_this.address+_this.addressDetail,
				"confirmText":"去支付",
				"success":function(res){
					if(res.confirm){
						_this.$ajax("/order/preferential/buy","POST",{skuId:1,count:_this.num18,receiver:_this.reciver,phone:_this.phone,province:_this.province,city:_this.city,area:_this.area,detail:_this.detail,defaultFlag:0}).then(res1=>{
							console.log(res1);
							if(res1.code == '200'){
								console.log(res1);
								uni.requestPayment({
									provider: "wxpay",
									appId: res1.paymentResult.payment.appId,
									timeStamp:res1.paymentResult.payment.timeStamp,
									nonceStr:res1.paymentResult.payment.nonceStr,
									package:res1.paymentResult.payment.package,
									signType:res1.paymentResult.payment.signType,
									paySign:res1.paymentResult.payment.paySign,
									success:function(){
										uni.navigateTo({
											"url":"/pages/shop/payment?orderNo="+res1.paymentResult.no
										})
										// uni.showToast({
										// 	"title":"下单成功"
										// })
										// uni.redirectTo({
										// 	url: '/pages/mine/bill'
										// });
									},
									fail:function(){
										uni.showToast({
											"title":"取消支付",
											"icon":"none"
										})
									}
								})
							}
						})
					}
				}
			})
			// setTimeout(function(){
			// 	uni.redirectTo({
			// 		"url":"../msdh/paymentsuccess"
			// 	})
			// })
			
		}
	}
}
